@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@400;500;600;700;800&display=swap);
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
*,*::after,*::before{box-sizing:border-box}li{list-style-type:none}p,h1,h2,h3,h4,h5,h6{margin:0}a{text-decoration:none;color:inherit}body{margin:0;padding:0;transition:all .25s linear;background-color:#e6faf5;color:#084c61;font-family:"Baloo Bhai 2",cursive}.App{display:flex;justify-content:center;align-items:center;flex-direction:column}.badge{background-color:#07beb877;text-align:center;height:-webkit-max-content;height:max-content;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:14px;padding:3px 10px;cursor:pointer;border-radius:13px}.button{background-color:#084c61;color:#fff;font-weight:500;line-height:1.1;width:-webkit-max-content;width:max-content;padding:13px 22px;margin:5px 10px 5px 0;border-radius:8px;outline:none;border:1px solid #084c61;font-size:15px;cursor:pointer;letter-spacing:2px;text-align:center;text-decoration:none;transition:box-shadow .2s ease-in;position:relative}
.clones{margin-top:60px;display:grid;grid-template-columns:50% 50%;width:80%;justify-items:center;justify-content:center;align-items:center;grid-gap:20px;gap:20px}@media(max-width: 1100px){.clones{grid-template-columns:50%}}
.card{min-height:612px;min-width:340px;border-radius:7px !important;border:1px solid #d9dbdf;box-shadow:5px 5px 5px #d9dbdf !important;background-color:#fff;display:flex;flex-direction:column}@media(max-width: 1100px){.card{min-height:auto}}.card .imagelink>img{width:100%;height:auto;object-fit:100%;border-bottom:1px solid #d9dbdf}.card .details{padding:12px;display:flex;flex-direction:column;align-items:baseline;justify-content:space-evenly;flex-grow:1}.card .details h3{text-align:center;font-size:23px;font-weight:900}.card .details p{text-align:justify;font-weight:500}.card .details .technologies{margin:5px 0}.card .details .technology{padding:2px;display:flex;flex-wrap:wrap}.card .details .technology>*{margin:5px 5px 5px 0}.card .details .buttons{display:flex;justify-content:center;align-items:center}
.title{width:70%;display:flex;align-items:center;flex-direction:column;margin:30px 5px}.title .details h1{width:100%;font-size:80px}.title .details p{text-align:right;font-weight:600}
